import React, { Component } from "react";

class TableHeader extends Component {
  raiseSort = (path, enableSort) => {
    if (!enableSort) return;
    const sortColumn = { ...this.props.sortColumn };
    if (sortColumn.path === path)
      sortColumn.order = sortColumn.order === "asc" ? "desc" : "asc";
    else {
      sortColumn.path = path;
      sortColumn.order = "asc";
    }

    this.props.onSort(sortColumn);
  };
  renderSortIcon = (column) => {
    if (column.path !== this.props.sortColumn.path) return null;
    if (this.props.sortColumn.order === "asc")
      return <i className="bi bi-sort-alpha-down" />;
    return <i className="bi bi-sort-alpha-up" />;
  };
  render() {
    return (
      <thead>
        <tr>
          {this.props.columns.map((column) => (
            <th
              className="clickable"
              key={column.path || column.key}
              onClick={() => this.raiseSort(column.path, column.enableSort)}
            >
              {column.label instanceof Function
                ? column.label(this.props.columnProps)
                : column.label}
              &nbsp;
              {this.renderSortIcon(column)}
            </th>
          ))}
        </tr>
      </thead>
    );
  }
}

export default TableHeader;
